.grid-container {
  margin-bottom: 5.5rem;
  display: grid;
  grid-template-columns: repeat(2, 10rem); //auto fill will leave empty cells
  grid-template-rows: repeat(
    auto-fit,
    10rem
  ); //auto fit will center cells and will share empty cells to center
  grid-gap: 1.1rem;
  justify-content: center;
  text-align: center;
  color: #f7f7f7;
}

.home-album-img {
  transition: all 0.5s ease-in-out;

  &:hover {
    filter: grayscale(100%);
  }
  width: 10rem;
  height: 10rem;
}

@media (min-width: 900px) {
  .home-album-img {
    width: 15rem;
    height: 15rem;
  }
  .grid-container {
    grid-template-columns: repeat(auto-fit, 15rem);
    grid-template-rows: repeat(auto-fit, 15rem);
  }
}

